.wrapper {
  height: 100%;
}
.wrapper :global(#map) {
  height: 60%;
  position: relative;
}
.wrapper :global(#map)::before,
.wrapper :global(#map)::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper :global(#map)::before {
  display: none;
  top: 0;
}
.wrapper :global(#map)::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper :global(#map)::before,
  .wrapper :global(#map)::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper :global(#map)::before,
  .wrapper :global(#map)::after {
    transform: scaleY(0.33);
  }
}
.wrapper_list {
  height: 40%;
}
.wrapper_list_item {
  padding: 10px 15px;
  position: relative;
}
.wrapper_list_item::before,
.wrapper_list_item::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_list_item::before {
  display: none;
  top: 0;
}
.wrapper_list_item::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_list_item::before,
  .wrapper_list_item::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_list_item::before,
  .wrapper_list_item::after {
    transform: scaleY(0.33);
  }
}
.wrapper_list_item_title {
  display: flex;
}
